import Vue from 'vue'
import Router from 'vue-router'
import store from '../store'
import { Message } from 'iview'
import socket from '../plugins/socket'
import login from '@index/views/Login'
import register from '@index/views/register'
import main from '@index/views/main'
import messages from '@index/views/messages'
import friends from '@index/views/friends'
import user from '@index/views/user'
import window from '@index/views/window'
import search from '@index/views/search'
import addFriend from '@index/views/addFriend'
import addModel from '@index/views/addModel'
import personInfo from '@index/views/personInfo'
import settings from '@index/views/settings'
import updatePersonInfo from '@index/views/updatePersonInfo'
Vue.use(Router)
const router = new Router({
  // mode: "hash",
  // base: process.env.BASE_URL,
  routes: [{
    path: "/",
    component: login,
    alias:"/login"
  }, {
    path: "/register",
    component: register
  }, {
    path: "/main",
    component: main,
    redirect: "/messages",
    meta: {
      requireAuth: true,
    },
    children: [{
      path: "/messages",
      component: messages,
      meta: {
        requireAuth: true,
      },
    }, {
      path: "/friends",
      component: friends,
      meta: {
        requireAuth: true,
      },
    },]
  }, {
    path: "/user",
    component:user,
    meta: {
      requireAuth: true,
    },
  },
  {
    path: "/chat",
    component: window,
    props: (route) => ({ query: route.query }),
    meta: {
      requireAuth: true,
    },
  },
  {
    path: "/searchfriends",
    component: search,
    meta: {
      requireAuth: true,
    },
  },
  {
    path: "/searchusers",
    component: addFriend,
    meta: {
      requireAuth: true,
    },
  },
  {
    path: "/addfriends",
    component: addModel,
    meta: {
      requireAuth: true,
    },
  },
  {
    path: "/settings",
    component: settings,
    meta: {
      requireAuth: true,
    },
  },
  {
    path: "/userinfo",
    component: personInfo,
    meta: {
      requireAuth: true,
    },
  },
  {
    path: "/updateuser",
    component: updatePersonInfo,
    meta: {
      requireAuth: true,
    },
  }
  ]
})
let index=0;
router.beforeEach((to, from, next) => {
  // to and from are both route objects. must call `next`.
  let { meta } = to;
  let loginstatus = store.getters.get_loginstatus
  let userinfo = store.getters.get_user

  if (meta && meta.requireAuth != null) {
    if (meta.requireAuth && loginstatus) {
      socket.emit("token")
      if(!index){
        store.dispatch("receive_message")
        index+=1
      }
      if (from.path == '/chat' || from.path == '/addfriends') {
        store.commit("SET_USERINFO")
      }
      next()
    } else {
      Message.warning({
        content: "账号未登录",
        onClose: () => {
          next("/login")
        }
      })
    }
  } else {
    next()
  }
})

export default router