import {mapMutations} from 'vuex'
export default {
    methods: {
        ...mapMutations(['SET_USERINFO']),
        loadChat(user) {
            this.SET_USERINFO(user)
            console.log(user);
            this.$router.push({
                path: "/chat", query: {userid:user.userId}
            })
        }
    },
}