<template>
  <Layout class="chat-view chat-add-friend">
    <Header class="header-warp">
      <Back>添加好友</Back>
    </Header>
    <Content class="content-warp">
      <Scroll :height="this.height">
        <Row>
          <Col :push="1" :span="22">
            <Input search v-model="searchvalue" @on-search="search" placeholder="输入需要添加的用户id或昵称"/>
          </Col>
        </Row>
        <Card class="card-pointer txt-center" dis-hover v-if="users.length==0">
          无数据
        </Card>
        <Card
          class="card-pointer"
          dis-hover
          v-for="(user, index) in users"
          :key="index"
          @click.native="user.isfriend?loadChat(user):addFriend(user)"
          v-else
        >
          <Row>
            <Col :lg="1" :md="3" :sm="3" :xs="5">
              <Avatar :src="`/MYCHAT/${user.imgurl}`" size="large"/>
            </Col>
            <Col :lg="23" :md="21" :sm="21" :xs="19">
              <div class="card-user-name">
                <span>{{user.nickname}}</span>
                <span>{{user.id}}</span>
              </div>
              <div class="card-user-title">
                <span class="gender">
                  <Icon :custom="!user.gender||user.gender=='男'?'mychat-icon-man':'mychat-icon-wuman'"/>{{user.age?user.age:100}}
                </span>
                <span>&nbsp;&nbsp;{{user.address?user.address:'未知'}}</span>
              </div>
            </Col>
          </Row>
        </Card>
        
      </Scroll>
    </Content>
  </Layout>
</template>

<script>
import "./styles/chatview.scss";
import height from "./mixins/height";
import {mapMutations} from 'vuex'
import chatwindow from './mixins/chatwindow'
import { searchUsersByIdOrUserName } from "@index/api/user.friend";

export default {
  mixins: [height,chatwindow],
  components: {
    Back: () => import("./components/back"),
  },
  data() {
    return {
      users: [],
      searchvalue: "",
    };
  },
  methods: {
    ...mapMutations(['SET_USERINFO']),
    search() {
      searchUsersByIdOrUserName({ value: this.searchvalue })
        .then(data => {
          this.users=data
        })
        .catch(error => {
          console.log(error);
        });
    },
    addFriend(userdata) {
      console.log(userdata);
      this.SET_USERINFO(userdata)
      this.$router.push({path:"/addfriends",params:{userdata:this.userdata}})
    }
  }
};
</script>

<style lang="scss">
.chat-add-friend {
  --footer-height:0px;
  .card-pointer {
    .ivu-card-body {
      .card-user-title {
        .gender {
          background: #c2c286;
          border-radius: 5px;
          padding: 1px;
          color: #000;
        }
      }
    }
  }
}
</style>
