<template>
  <Layout class="chat-search chat-view">
    <Header class="header-warp">
      <Back>搜索</Back>
    </Header>
    <Content class="content-warp">
      <Scroll :height="this.height">
        <Row>
          <Col :push="1" :span="22">
            <Input search @on-search="search" v-model="searchvalue" placeholder="输入查询的用户id或昵称"/>
          </Col>
        </Row>
        <Card class="card-pointer txt-center" dis-hover v-if="friends.length==0">无数据</Card>
        <Card
          class="card-pointer"
          dis-hover
          v-for="(friend, index) in friends"
          :key="index"
          @click.native="loadChat(friend)"
          v-else
        >
          <Row>
            <Col :lg="1" :md="3" :sm="3" :xs="5">
              <Avatar :src="`/MYCHAT/${friend.imgurl}`" size="large"/>
            </Col>
            <Col :lg="23" :md="21" :sm="21" :xs="19">
              <div class="card-user-name">
                <span>{{friend.nickname}}</span>
                <span>{{friend.id}}</span>
              </div>
              <div class="card-user-title">{{friend.message}}</div>
            </Col>
          </Row>
        </Card>
      </Scroll>
    </Content>
  </Layout>
</template>

<script>
import "./styles/chatview.scss";
import { searchFriendsByIdOrUserName } from "@index/api/user.friend";
import chatwindow from "./mixins/chatwindow";
import height from "./mixins/height";
export default {
  mixins: [chatwindow, height],
  props: {
    visible: {
      default: false,
      type: Boolean
    }
  },
  components: {
    Back: () => import("./components/back")
  },
  data() {
    return {
      friends: [],
      searchvalue: ""
    };
  },
  methods: {
    search() {
      searchFriendsByIdOrUserName({ value: this.searchvalue })
        .then(data => {
          this.friends = data;
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>
<style lang="scss" scoped>
.chat-search {
  --footer-height:0px;
}
</style>


