import socket from "@index/plugins/socket";
import { loadUnreadMsg, loadLast20sMsgsByUser } from '@index/api/user.message'
import { dateFormat } from '@/lib/utils/date'
import {Spin} from 'iview'
const state = {
    unreadnum: 0,//未读信息量
    userinfo: {},//当前聊天的人的对象信息
    chatList: [],//当前对象聊天记录
    messages: [],//初始化时未读消息
    sendmessage: {},//发送信息
    remessage: {},//接受到的消息
}

const mutations = {
    SET_USERINFO(state, userinfo = {}) {
        state.userinfo = userinfo
    },
    SET_UNREADNUM(state, num) {
        state.unreadnum = num>=0?num:0
    },
    SET_CHATLIST(state, chatList = []) {
        state.chatList = chatList
    },
    SET_MESSAGES(state, messages) {
        state.messages = messages
    },
    SET_SENDMESSAGE(state, sendmessage = "") {
        state.sendmessage = sendmessage
    },
    SET_REMESSAGE(state, remessage = "") {
        state.remessage = remessage
    },
}

const actions = {
    send_message({ commit, state, getters }, sendmessage) {
        let user = getters.get_user
        let sendDate = dateFormat(new Date(), "YYYY-MM-DD HH:mm:ss")
        let sendmessageobj = { receiverId: state.userinfo.userId, content: sendmessage, sendDate }
        socket.emit("send", sendmessageobj)
        let chatlist = state.chatList
        chatlist.push({ sendDate, receiverId: state.userinfo.userId, content: sendmessage, senderId: user.userId })
        commit("SET_CHATLIST", chatlist)
        // commit("SET_SENDMESSAGE", sendmessageobj)
    },
    add_friend({ commit, state }, sendmessage) {
        let user = getters.get_user
        let sendDate = dateFormat(new Date(), "YYYY-MM-DD HH:mm:ss")
        let sendmessageobj = { receiverId: state.userinfo.userId, content: sendmessage, sendDate }
        socket.emit("addfriend", sendmessageobj)
        let chatlist = state.chatList
        chatlist.push({ sendDate, receiverId: state.userinfo.userId, content: sendmessage, senderId: user.userId })
        commit("SET_CHATLIST",chatlist)
        commit("SET_SENDMESSAGE", sendmessageobj)
    },
    receive_message({ commit, state, getters }) {
        socket.on("receive", obj => {
            let userinfo = state.userinfo;
            let unreadnum = state.unreadnum;
            let messages = state.messages;
            //发送者是否为当前聊天对象
            let findindex = 0;
            let findmessage = messages.find((item, index) => {
                findindex = index
                return item.userId == obj.userId
            })
            let receivemsg = { ...obj, date: obj.sendDate,sendDate:obj.sendDate, userId: obj.userId }
            //判断是否为已存储信息列表
            if (findmessage != null) {
                messages[findindex].content = obj.content;
                messages[findindex].date = obj.sendDate;
            } 
            //如果当前聊天对象为发送信息对象
            if (userinfo && obj.senderId == userinfo.userId) {
                //为消息队列加未读信息number为0
                messages.unshift({ ...receivemsg, number: 0 })
            } else {
                if (findmessage == null) {
                    messages.unshift({ ...receivemsg, number: 1 })
                } else {
                    console.log(messages[findindex].number);
                    messages[findindex].number = Number(messages[findindex].number) + 1;
                }
                commit("SET_UNREADNUM", Number(unreadnum) + 1)
            }
            console.log(messages);
            let chatlist = state.chatList;
            chatlist.push(receivemsg);
            commit("SET_CHATLIST", chatlist)
        })
    },
    get_chatlist({ commit, state }) {
        let messages = state.messages;
        let userinfo = state.userinfo;

        let userId = userinfo.userId;
        let unreadnum = state.unreadnum;
        let findindex = 0;
        let findmessage = messages.find((item, index) => {
            findindex = index
            return item.userId == userId
        })
        Spin.show();
        loadLast20sMsgsByUser({ userid: userId }).then(data => {
            commit("SET_CHATLIST", data)
            let lastchat = data[data.length - 1];
            if (lastchat != null) {
                if (findmessage == null) {
                    messages.unshift({ ...userinfo, content: lastchat.content, date: lastchat.sendDate,sendDate:lastchat.sendDate })
                } else {
                    commit("SET_UNREADNUM", Number(unreadnum) - Number(findmessage.number))
                    messages[findindex].number = 0
                }
            }
        }).catch((error) => {
            console.log(error);
        }).finally(()=>{
            Spin.hide()
        })
    },
    set_messages({ commit }) {
        commit("SET_SENDMESSAGE")
        commit("SET_CHATLIST")
        loadUnreadMsg().then(data => {
            let unreadnum = 0
            data.forEach(item => {
                unreadnum = unreadnum + Number(item.number)
            })
            commit("SET_UNREADNUM", Number(unreadnum))
            commit("SET_MESSAGES", data)
        }).catch(error => { console.log(error); })
    }
}

const getters = {
    get_chatlist(state) {
        return state.chatList
    },
    get_messages(state) {
        return state.messages
    },
    get_chat_userinfo(state) {
        return state.userinfo
    },
    get_unreadnum(state) {
        return state.unreadnum
    }
}

export default {
    state,
    mutations,
    actions,
    getters
}