<template>
  <Layout class="chat-add-model chat-view">
    <Header class="header-warp">
      <Back>
        好友验证信息
        <template #rightbutton>
          <Button type="primary" @click="sendAddRequest">发送</Button>
        </template>
      </Back>
    </Header>
    <Content>
      <Row>
        <Col :push="1" :lg="12" :xs="22">
          <Form>
            <div style="font-size:1.2rem">向{{userdata.nickname}}发送添加好友信息</div>
            <FormItem>
              <Input type="textarea" v-model="msg"></Input>
            </FormItem>
          </Form>
        </Col>
      </Row>
    </Content>
  </Layout>
</template>

<script>
import './styles/chatview.scss'
import {mapGetters,mapActions} from 'vuex'
import { addNewFriends } from "@index/api/user.friend";
export default {
  props: {
  },
  computed:{
    ...mapGetters({'userinfo':'get_user','userdata':'get_chat_userinfo'}),
  },
  data() {
    return {
      msg: ""
    }
  },
  mounted(){
    console.log(this.userdata);
    this.msg="我是"+(this.userinfo.name!=null?this.userinfo.name:this.userinfo.nickname);
  },
  methods:{
    ...mapActions(['add_friend']),
    sendAddRequest(){
      let {userId,nickname} = this.userdata;
      console.log(userId);
      let msg= this.msg;
      addNewFriends({userId,msg}).then((data) => {
        this.$Message.success("发送成功，您已可以与"+nickname+"聊天")
        this.add_friend(msg)
      }).catch((err) => {
        console.log(err);
      });
    }
  },
  components: {
    Back: () => import("./components/back")
  }
};
</script>

<style lang="scss" scoped>
.chat-add-model{
  --footer-height:0px;
}
</style>
