<template>
  <div>
    <Card
      class="card-pointer"
      dis-hover
      v-for="(message, index) in messages"
      :key="index"
      @click.native="loadChat(message)"
    >
      <Badge :count="Number(message.number)" style="display:block;margin-right:10px">
        <Row>
          <Col :lg="1" :md="3" :sm="3" :xs="5">
            <Avatar :src="`/MYCHAT/${message.imgurl}`" size="large"/>
          </Col>
          <Col :lg="23" :md="21" :sm="21" :xs="19">
            <div class="card-user-name">{{message.nickname}}</div>
            <div class="card-user-title float-rt"><Time :time="message.date" /></div>
            <div class="card-user-title">{{message.content}}</div>
          </Col>
        </Row>
      </Badge>
    </Card>
  </div>
</template>

<script>

import chatwindow from './mixins/chatwindow'
import { mapGetters } from 'vuex';
export default {
  mixins:[chatwindow],
  data() {
    return {
    };
  },
  computed: {
    ...mapGetters({'messages':'get_messages'})
  },
};
</script>

