<template>
  <Layout class="chat-settings chat-view">
    <Header class="header-warp">
      <Back>设置</Back>
    </Header>
    <Content>
      <Card class="card-pointer" style="padding:0">
        <CellGroup>
          <Cell extra="中文" @click.native="languageShow">
            <span class="card-user-name">语言</span>
          </Cell>
          <Button type="error" long  @click.native.stop="loginout" :loading="logoutstatus" :disabled="logoutstatus"> <Icon custom="mychat-icon-loginout"/>退出登录</Button>
        </CellGroup>
      </Card>
    </Content>
    <awesome-picker
      ref="languagepicker"
      :textTitle="languagepicker.textTitle"
      :data="languagepicker.data"
      :anchor="languagepicker.anchor"
      @confirm="handleLanguagePickerConfirm"
    ></awesome-picker>
  </Layout>
</template>

<script>
import "./styles/chatview.scss";
import { loginout } from "@index/api/sys.loginandregister";
import { mapActions } from "vuex";
export default {
  props: {
    visible: {
      default: false,
      type: Boolean
    }
  },
  components: {
    Back: () => import("./components/back"),
    ForWard: () => import("./components/forward")
  },
  data() {
    return {
      logoutstatus:false,
      nowselectdata: {},
      resultselectshow: false,
      languagepicker: {
        anchor: [],
        textTitle: "语言选择",
        data: [{ value: "中文" }, { value: "英文" }]
      }
    };
  },
  methods: {
    ...mapActions(["logout"]),
    languageShow() {
      this.$refs.languagepicker.show();
    },
    loginout(){
      this.logoutstatus=true
      this.logout().finally(()=>{
        this.logoutstatus=false
      });
    },
    handleLanguagePickerConfirm(v) {
      this.languagepicker.anchor = v;
      this.value = v ? JSON.stringify(v) : null;
    }
  }
};
</script>

<style lang="scss">
.chat-settings {
  --footer-height:0px;
  .loginout {
    background-color: #ee3f3f;
    color: #fff;
  }
}
</style>
