import Vue from 'vue'
import socketio from 'socket.io-client'
import store from '@index/store'
let socket=socketio.connect(process.env.VUE_APP_socketUrl, {transports:["websocket"]})

let emit=function (en,data={}){
    let user=store.getters.get_user
    user&&user.userId?socket.emit(en,{...data,senderId:user.userId}):""
}
let on=function (el,fun){
    socket.on(el,fun)
}
console.log(socket.on);
export default {
    emit:emit,
    on:on
}