export default {
  data() {
    return {
      height: 0,
      resizebool: false
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.loadheight()
    });
  },
  updated() {
    this.$nextTick(() => {
      this.loadheight()
    });
  },
  methods: {
    loadheight() {
      let vm = this;
      if (this.$el.querySelector) {
        let contentwarp = this.$el.querySelector(".content-warp")
        vm.height = contentwarp.offsetHeight;
        window.onresize = function () {
          if (!vm.resizebool) {
            vm.resizebool = true;
            setTimeout(() => {
              vm.height = contentwarp.offsetHeight;
              vm.resizebool = false;
            }, 200);
          }
        };
      }
    }
  },
}