import '@index/plugins/axios'

export function loadFriends(){
    return axios.post("/selectFriendInfo")
}

export function addNewFriends({userId,msg}){
    return axios.post("/addfriend",{adduserid:userId,msg})//
}

export function searchFriendsByIdOrUserName({value}){
    return axios.post("/selectFriendInfoById",{value})
}

export function searchUsersByIdOrUserName({value}){
    return axios.post("/selectUserByNickName",{value})
}

