<template>
  <Layout class="chat-view chat-personinfo">
    <Header class="header-warp">
      <Back>
        个人信息
        <template #rightbutton>
          <Button type="text" size="large" ghost to="/updateuser">修改信息</Button>
        </template>
      </Back>
    </Header>
    <Content>
      <CellGroup>
        <Upload
          ref="upload"
          id="upload"
          @focus="progressHandler"
          :action="uploadaction"
          :on-progress="progressHandler"
          :on-success="successHandler"
          :on-error="errorHandler"
          :with-credentials="true"
          :show-upload-list="false"
        >
          <Card class="card-pointer" style="width:100vw;">
            <Row class="card-user-name aviter">
              <span>头像</span>
              <i-circle
                i-circle
                :percent="fileuploadprogroess"
                :stroke-color="fileuploadprogroess==100?'#5cb85c':'#ff0'"
                :size="100"
                class="float-rt"
              >
                <Avatar
                  :src="`/MYCHAT/${userinfo.imgurl}?${new Date()}`"
                  style="width:90px;height:90px;border-radius:50px"
                />
                <!-- <Avatar :src="`/api/${userinfo.imgurl}`" size="large"/> -->
              </i-circle>
            </Row>
          </Card>
        </Upload>
        <Cell title="昵称" :extra="userinfo.nickname"></Cell>
        <Cell title="mychat号" :extra="userinfo.userId"></Cell>
        <Cell title="年龄" :extra="`${userinfo.age}`"></Cell>
        <Cell title="性别" :extra="userinfo.gender"></Cell>
        <Cell title="地区" :extra="userinfo.address"></Cell>
      </CellGroup>
    </Content>
  </Layout>
</template>

<script>
import "./styles/chatview.scss";
import { mapGetters, mapActions } from "vuex";
export default {
  props: {
    visible: {
      default: false,
      type: Boolean
    }
  },
  computed: {
    ...mapGetters({ userinfo: "get_user" })
  },
  data() {
    return {
      updateshow: false,
      fileuploadprogroess: 0,
      fileuploadstatus: false,
      uploadaction: process.env.VUE_APP_apiUrl + "/updateImg"
    };
  },
  methods: {
    progressHandler(file, fileList) {
      console.log(file);
      this.fileuploadprogroess = file.percent;
    },
    successHandler(response, file, fileList) {
      this.fileuploadprogroess = 0;
      this.$Message.success("上传成功")
    },
    errorHandler(error, file, fileList) {
      this.fileuploadprogroess = 0;
      this.$Message.error("上传失败"+error)
    }
  },
  components: {
    Back: () => import("./components/back")
  }
};
</script>

<style lang="scss">
.chat-personinfo {
  --footer-height: 0px;
}
</style>
