<template>
  <Layout class="chat-view chat-update-user-info">
    <Header class="header-warp">
      <Back>
        修改个人信息
        <template #rightbutton>
          <Button type="primary" @click.native="submit">提交</Button>
        </template>
      </Back>
    </Header>
    <Content>
      <Form :label-width="40" :data="updateUser">
        <Card>
          <FormItem label="姓名" class="card-user-name">
            <Input v-model="updateUser.name"></Input>
          </FormItem>
          <FormItem label="昵称" class="card-user-name">
            <Input v-model="updateUser.nickname"></Input>
          </FormItem>
          <FormItem label="性别" class="card-user-name">
            <RadioGroup v-model="updateUser.gender">
              <Radio label="男"></Radio>
              <Radio label="女"></Radio>
            </RadioGroup>
          </FormItem>
          <FormItem
            label="年龄"
            class="card-user-name"
            @click.native="pickerShow('datePicker')"
          >{{updateUser.age}}</FormItem>
          <FormItem
            label="地区"
            class="card-user-name"
            @click.native="pickerShow('areaPicker')"
          >{{updateUser.address}}</FormItem>
        </Card>
      </Form>
    </Content>
    <awesome-picker
      ref="datePicker"
      :textTitle="pickerdata.date.textTitle"
      :type="pickerdata.date.type"
      :data="pickerdata.date.data"
      :anchor="pickerdata.date.anchor"
      @confirm="handleDatePickerConfirm"
    ></awesome-picker>
    <awesome-picker
      ref="areaPicker"
      :textTitle="pickerdata.area.textTitle"
      :data="pickerdata.area.data"
      :anchor="pickerdata.area.anchor"
      @confirm="handleAreaPickerConfirm"
    ></awesome-picker>
  </Layout>
</template>

<script>
import "./styles/chatview.scss";
import area from "./js/area";
import { mapGetters, mapActions } from "vuex";
import { updateUserInfo } from "@index/api/user";
export default {
  props: {
    visible: {
      default: false,
      type: Boolean
    },
    data: {
      default: () => {
        return {};
      },
      type: Object
    },
    onClick: {
      type: Function,
      default: () => {
        new Function();
      }
    }
  },
  data() {
    return {
      pickerdata: {
        date: {
          anchor: [],
          textTitle: "年龄选择",
          data: new Array(100).fill(0).map((item, index) => ({ value: index }))
          // type:'date'
        },
        area: {
          anchor: [],
          textTitle: "地区选择",
          data: area
        }
      }
    };
  },
  computed: {
    ...mapGetters({ userinfo: "get_user" }),
    updateUser() {
      return JSON.parse(JSON.stringify(this.userinfo));
    }
  },
  components: {
    Back: () => import("./components/back")
  },
  methods: {
    ...mapActions(["update_user"]),
    pickerShow(pickerrefname) {
      this.$refs[pickerrefname].show();
    },
    datePickershow(pickerrefname) {
      let age = this.updateUser.age;
      this.pickerdata.date.author = [{ index: age, value: age }];
      this.pickerShow(pickerrefname);
    },
    areaPickershow(pickerrefname) {
      let area = this.updateUser.area;
      area = area.split("-");
      this.pickerdata.date.author = area.map(item => {
        return { value: item };
      });
      this.pickerShow(pickerrefname);
    },
    handleDatePickerConfirm(v) {
      this.pickerdata.date.anchor = v;

      this.updateUser.age = v ? v[0].value : null;
    },
    handleAreaPickerConfirm(v) {
      this.pickerdata.area.anchor = v;
      let value = "";
      v.map((item, index) => {
        value += (index ? "-" : "") + item.value;
      });
      this.updateUser.address = value;
    },
    submit() {
     this.update_user(this.updateUser)
    }
  }
};
</script>

<style lang="scss">
.chat-update-user-info {
  --footer-height: 0px;
  .ivu-form-item {
    margin-bottom: 20px;
  }
}
</style>
