<template>
  <Row class="user_box">
    <Col class="container" :xs="{span:24}" :md="{span:12,push:6}" :lg="{span:6,push:9}">
      <Card class="register-form" :bordered="false" dis-hover>
        <div slot="title">
          <h3>
            <span>注册帐号</span>
            <Button class="float-rt" type="text">
              <a id="index" href="#/login">返回登录页面</a>
            </Button>
          </h3>
        </div>
        <Form :model="newuser" :rules="rules" ref="regform">
          <FormItem prop="tel">
            <Input
              size="large"
              type="text"
              v-model.trim="newuser.tel"
              placeholder="请输入11位手机号"
              suffix="admin-icon-user"
            ></Input>
          </FormItem>
          <FormItem prop="code">
            <Input size="large" type="text" v-model.trim="newuser.code" placeholder="请输入验证码">
              <template slot="append">
                <Button
                  type="primary"
                  @click.native="loginCode"
                  v-text="codeinner"
                  :disabled="codedis"
                ></Button>
              </template>
            </Input>
          </FormItem>
          <FormItem prop="username">
            <Input size="large" type="text" v-model.trim="newuser.username" placeholder="请输入用户昵称"></Input>
          </FormItem>
          <FormItem prop="pass">
            <InputPassword
              size="large"
              :value.sync="newuser.pass"
              placeholder="请输入密码,密码为字母、数字和符号两种及以上的组合6-16个字符"
            ></InputPassword>
          </FormItem>
          <FormItem prop="password">
            <InputPassword
              size="large"
              :value.sync="newuser.password"
              placeholder="再次输入密码"
              @input.native="inputres"
            ></InputPassword>
          </FormItem>
          <FormItem>
            <Col :xs="{span:24}" :md="{span:10,push:1}" style="margin-bottom:0.5rem">
              <Button type="primary" @click.native="submits" long>注册帐号</Button>
            </Col>
            <Col :xs="{span:24}" :md="{span:10,push:2}">
              <Button type="error" @click.native="formreset($refs['regform'])" long>重置</Button>
            </Col>
          </FormItem>
        </Form>
      </Card>
    </Col>
  </Row>
</template>

<script>
import { loadCode } from "@index/api/sys.code";
import { register } from "@index/api/sys.loginandregister";
import form from "@index/mixins/form";
export default {
  mixins: [form],
  data() {
    let vm = this;
    return {
      newuser: {
        tel: "",
        code: "",
        username: "",
        pass: "",
        password: ""
      },
      rules: {
        tel: [
          {
            message: "手机号不能为空",
            required: true,
            trigger: "blur"
          },
          {
            name: "手机号",
            validator: vm.$validateApi.tel,
            trigger: "blur"
          }
        ],
        code: [
          {
            message: "验证码不能为空",
            required: true,
            trigger: "blur"
          }
        ],
        username: [
          {
            required: true,
            message: "用户昵称不能为空",
            trigger: "blur"
          },
          {
            name: "用户昵称",
            len: 6,
            validator: vm.$validateApi.cnLength,
            trigger: "blur"
          }
        ],
        pass: [
          {
            required: true,
            message: "密码不能为空",
            trigger: "blur"
          },
          {
            validator: vm.$validateApi.password,
            trigger: "blur"
          }
        ],
        password: [
          {
            value: "",
            validator: vm.$validateApi.validateRepass,
            trigger: "blur"
          }
        ]
      },
      codedis: false,
      codeinner: "获取验证码",
      errormsg: ""
    };
  },
  components: {
    InputPassword: () => import("@/components/inputPassword.vue")
  },
  methods: {
    inputres() {
      var vm = this;
      vm.rules.password[0].value = vm.newuser.pass;
    },
    loginCode() {
      let vm = this;

      let tel = vm.newuser.tel;      console.log(tel);
      if (vm.codedis) {
        vm.$Message.error("请等待一段时间后再试");
      } else {
        loadCode({ tel })
          .then(() => {
            vm.codeDis();
            vm.$Message.success({
              duration: 1,
              content: "验证码已发送，请注意查收"
            });
          })
          .catch(error => {
            console.log(error);
          });
      }
    },
    codeDis() {
      let vm = this;
      vm.codedis = true;
      let i = 10;
      upText();
      let times = setInterval(upText, 1000);
      function upText() {
        vm.codeinner = i + "秒后继续";
        i -= 1;
        if (i <= -1) {
          clearInterval(times);
          vm.codeinner = "未收到？再次获取";
          vm.codedis = false;
        }
      }
    },
    submits() {
      let vm = this;
      let newuser = vm.newuser;
      vm.$refs["regform"].validate(bool => {
        if (bool) {
          register(newuser)
            .then(success => {
              vm.$Message.success({
                duration: 0.8,
                content: "注册成功",
                onClose() {
                  vm.$router.push("/login");
                }
              });
            })
            .catch(error => {})
            .finally(() => {
              vm.loading = false;
            });
        } else {
          vm.loading = false;
        }
      });
    }
  }
};
</script>

<style scope="scoped" lang="scss">
.user_box {
  .container {
    position: absolute;
    // top: 50%;
    left: 50%;
    transform: translate(-50%,25%);
    max-width: 600px;
    margin: auto;
    .register-form {
      // border: 1px solid rgba(230, 228, 123, 0.466);
      padding: 0.5rem;
      // box-shadow: 0.5rem 0.5rem 0.2rem rgba(178, 199, 61, 0.37);
    }
  }
}
</style>
