<template>
  <div>
    <Card
      class="card-pointer"
      dis-hover
      v-for="(friend, index) in friends"
      :key="index"
      @click.native="loadChat(friend)"
    >
      <Row>
        <Col :lg="1" :md="3" :sm="3" :xs="5">
          <Avatar :src="`/MYCHAT/${friend.imgurl}`" size="large"/>
        </Col>
        <Col :lg="23" :md="21" :sm="21" :xs="19">
          <div class="card-user-name">{{friend.nickname}}</div>
          <!-- <div class="card-user-title">123</div> -->
        </Col>
      </Row>
    </Card>
  </div>
</template>

<script>
import chatwindow from './mixins/chatwindow'
import { loadFriends } from "@index/api/user.friend";
export default {
  mixins:[chatwindow],
  data() {
    return {
      friends: []
    };
  },
  created() {
    loadFriends()
      .then(data => {
        this.friends = data;
      })
      .catch(error => {
        console.log(error);
      });
  }
};
</script>

