<template>
  <Layout class="chat-view chat-window" :style="`--footer-height:${footerHeight}px`">
    <Header class="header-warp">
      <Back>{{friendinfo.nickname}}</Back>
    </Header>
    <Content class="content-warp">
      <Scroll ref="scroll" :height="this.height">
        <div class="chat-msg-list">
          <div class="chat-msg-list-item" v-for="(chatlist,index) in chatlists" :key="index">
            <div class="msg-time-text"><Time :time="chatlist.sendDate" /></div>
            <div :class="chatlist.senderId==userinfo.userId?'text-right':'text-left'">
              <div class="msg-list-item-icon">
                <Avatar
                  :src="`/MYCHAT/${chatlist.senderId==userinfo.userId?userinfo.imgurl:friendinfo.imgurl}`"
                  size="large"
                />
              </div>
              <p class="msg-list-item-text">{{chatlist.content}}</p>
            </div>
          </div>
        </div>
      </Scroll>
    </Content>
    <Footer class="footer-warp" ref="footer" :style="{'height':footerHeight+'px'}">
      <Card>
        <Row>
          <Col :span="20">
            <Input
              type="textarea"
              ref="textarea"
              :autosize="{ minRows: 1, maxRows: 6 }"
              @on-keyup.enter="send"
              v-model="sendmsg"
            ></Input>
          </Col>
          <Col :span="2" class="chat-send">
            <Button :style="`height:${textareaheight}px`" @click="send">提交</Button>
          </Col>
        </Row>
      </Card>
    </Footer>
  </Layout>
</template>

<script>
import "./styles/chatview.scss";
import height from "./mixins/height";
import { mapGetters, mapActions } from "vuex";
import { dateFormat } from "@/lib/utils/date";
import socket from "@index/plugins/socket";
export default {
  mixins: [height],
  props: {
    chatuserid: {
      default: "",
      type: String
    }
  },
  computed: {
    ...mapGetters({
      userinfo: "get_user",
      chatlists: "get_chatlist",
      friendinfo: "get_chat_userinfo"
    })
  },
  components: {
    Back: () => import("./components/back")
  },
  data() {
    return {
      footerHeight: 68,
      textareaheight: 0,
      textoffsetTop: 0,
      sendmsg: ""
    };
  },
  props: {
    query: {
      default: () => null,
      type: Object
    }
  },
  mounted() {
    socket.emit("token");
    this.get_chatlist();
    this.$nextTick(() => {
      this.footerHeight = this.$refs.footer.$el.offsetHeight;
      this.textareaheight = this.$refs.textarea.$el.offsetHeight;
      this.textoffsetTop = this.footerHeight - this.textareaheight;
      this.updateMsgAndSendHeight();
    });
  },
  methods: {
    dateFormat,
    ...mapActions(["send_message", "get_chatlist"]),
    /**
     * 修改信息显示栏和发送信息栏目的高度
     *
     */
    updateMsgAndSendHeight() {
      let scrollcontainer = this.$refs.scroll.$el.querySelector(
        ".ivu-scroll-container"
      );
      let scrollHeight = scrollcontainer.scrollHeight;
      let offsetHeight = scrollcontainer.offsetHeight;
      scrollcontainer.scrollTop = scrollHeight - offsetHeight;
      this.loadheight();
    },
    send() {
      this.send_message(this.sendmsg);
      this.sendmsg="";
      this.$nextTick(()=>{
        this.updateMsgAndSendHeight()
      })
    }
  },
  watch:{
    chatlists(news){
       this.$nextTick(()=>{
        this.updateMsgAndSendHeight()
      })
    }
  }
};
</script>

<style lang="scss">
.chat-window {
  --footer-height: 68px;
  line-height: 1.5;
  .footer-warp {
    transition: height 0.2s;
  }
  .chat-msg-list {
    padding: 10px;
    .chat-msg-list-item {
      padding: 5px;
      margin-bottom: 10px;
      white-space: pre-line;
      word-wrap: break-word;
      .msg-list-item-text {
        position: relative;
        border-radius: 7px;
        left: 10px;
        background-color: #a6e860;
        padding: 6px 10px 8px 10px;
        // z-index: 1;
      }
      .msg-time-text {
        text-align: center;
      }
      .text-left,
      .text-right {
        display: flex;
        width: 60%;
      }
      .text-left {
        &:before {
          box-sizing: border-box;
          position: relative;
          left: -10px;
          top: 9px;
        }
        .msg-list-item-text {
          background-color: #f7e5a7;
          &:before {
            content: "";
            display: block;
            width: 0;
            height: 0;
            border: 8px solid transparent;
            border-right: 8px solid #f7e5a7;
            position: absolute;
            top: 8px;
            left: -16px;
          }
        }
      }
      .text-right {
        flex-direction: row-reverse;
        margin-left: 40%;
        &:after {
          box-sizing: border-box;
          position: relative;
          right: -10px;
          top: 9px;
        }
        .msg-list-item-text {
          left: -10px;
          &:after {
            content: "";
            display: block;
            width: 0;
            height: 0;
            border: 8px solid transparent;
            border-left: 8px solid #a6e860;
            position: absolute;
            top: 8px;
            right: -16px;
          }
        }
      }
    }
  }
}
</style>
