import '@index/plugins/axios'
/**
 * 查询出当前用户未查看的全部信息
 * 
 * @export
 * @returns
 */
export function loadUnreadMsg() {
    return axios.post("/readUnreadMessage")
}

/**
 * 根据friendid查询两个人之间最近的20条聊天记录
 *
 * @export
 * @param {String} { userid }
 * @returns
 */
export function loadLast20sMsgsByUser({ userid }) {
    return axios.post("/selectByTime", { userid })
}